import { useState } from 'react';
import { Game } from '../components/game';
import { Splash } from '../components/splash';

const pages = { splash: 'splash', game: 'game' };

export default function Index () {
  const [page, setPage] = useState(pages.splash);
  let content = <div>Unknown UI error</div>;
  if (page === pages.splash) {
    return <Splash onPlayNow={() => setPage(pages.game)} />;
  } else if (page === pages.game) {
    return <Game />;
  }

  return (
    <>
      {content}

      <GlobalStyles />
    </>
  );
}

function GlobalStyles () {
  return (
    <style jsx global>{`
      html, body, body > div:first-child, div#__next, div#__next > div {
        margin: 0;
        padding: 0;
        height: 100%;
        overflow: hidden;
      }

      body {
        background: #430d27;
        color: #1c002e;
        font-family: 'Open Sans', sans-serif;
      }
    `}</style>
  );
}

